import React from 'react';
import { EditableArea } from '@magnolia/react-editor';

const Content = props => {
  const { top, main, bottom, title } = props;

  return (
    <div className="content-page">
      <div className="hint">[Content Page]</div>
      <h1>{title || props.metadata['@name']}</h1>

      <div className="top">
        <div className="hint">[Top Area]</div>         
        {top && <EditableArea className="Area" content={top} />}  
      </div>

      <main>
        <div className="hint">[Main Area]</div>         
        {main && <EditableArea className="Area" content={main} />}  
      </main>

      <div className="bottom" >
        <div className="hint">[Bottom Area]</div>
        {bottom && <EditableArea className="Area" content={bottom} />}  
      </div>    
    </div>
  ) 
};

export default Content;
